<script>
import Navbar from "@/components/Navbar.vue";
import BannerSwiper from "@/components/BannerSwiper.vue";
import PeopleGlassSwiper from "./PeopleGlassSwiper.vue";
import Footer from "@/components/Footer.vue";
import { Swiper, SwiperSlide } from "swiper/vue";
import "swiper/css";
import "swiper/css/pagination";
import { Pagination } from "swiper/modules";
import { ref, watchEffect } from "vue";

export default {
  components: {
    Navbar,
    BannerSwiper,
    Footer,
    Swiper,
    SwiperSlide,
    PeopleGlassSwiper,
  },
  setup() {
    const nextSection = ref(null);

    const scrollToNextSection = () => {
      if (nextSection.value) {
        window.scrollTo({
          top: nextSection.value.offsetTop,
          behavior: "smooth",
        });
      }
    };

    watchEffect(() => {
      document.querySelectorAll(".neonText").forEach(function (element) {
        element.innerHTML = element.textContent
          .split("")
          .map(function (char) {
            return "<span>" + char + "</span>";
          })
          .join("");
      });
    });

    return {
      nextSection,
      scrollToNextSection,
      modules: [Pagination],
    };
  },

  data() {
    return {
      featuredProducts: [
        // 示例数据
        {
