<script>
export default {
  data() {
    return {
      cartNumber: 0,
      isMenuOpen: false,
    };
  },
  methods: {
    getCard() {
      const api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_API_NAME}/cart`;
      this.$http.get(api).then((res) => {
        if (res.data.success) {
          console.log(res);

          this.cartNumber = res.data.data.carts.length;
        }
      });
    },
    toggleMenu() {
      this.isMenuOpen = !this.isMenuOpen;
      if (this.isMenuOpen) {
        document.body.style.overflow = "hidden";
      } else {
        document.body.style.overflow = "";
      }
    },
  },


  created() {
    this.getCard();
    this.$nextTick(() => {
    if (!this.isMenuOpen) {
      document.body.style.overflow = "";
    }
  });
   
  },
};
</script>
